import React from "react";
import "./main-area.css";
import {Card,Row,Col} from "react-bootstrap"
function Mainarea() {
  return (
    <div>
      <div className="main-area">
        <h1>Statistics</h1>
        <div className="btn-area">
          <button className="country-btn">My Country</button>
          <button className="global-btn">Global</button>
        </div>
        <div className="display-area col-xs-12">

    <Row xs={2} md={4} className="g-4">
  
    <Col>
      <Card className="cards-main Affected">
        <Card.Body>
        <h5>Affected</h5>
         <h2>3323235</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main Death">
        <Card.Body>
        <h5>Death</h5>
         <h2>2323235</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main recover ">
        <Card.Body>
        <h5>Recovered</h5>
         <h2>3323235</h2>
        </Card.Body>
      </Card>
    </Col>
    <Col>

      <Card className="cards-main active ">
        <Card.Body>
        <h5>Active</h5>
         <h2>2323235</h2>
        </Card.Body>
      </Card>
    </Col>
  
</Row>
          
        </div>

        

      </div>
    </div>
  );
}

export default Mainarea;
